StaticHtml.Views = StaticHtml.Views || {};

(function() {
	'use strict';

	StaticHtml.Views.EmailForm = Backbone.View.extend({

		events: {
			'submit': 'submitEmailForm',
			'keydown #bb-emailInput': 'formReset'
		},

		email_feedbacks: null,

		input: $('#bb-emailInput'),

		initialize: function(options) {
			this.email_feedbacks = options.email_feedbacks;
		},

		validateEmail: function(value) {
			var mailformat = /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i;
			if(mailformat.test(value)) {
				// formato email corretto
				return true;
			} else {
				this.formError(this.email_feedbacks.email_error);
				return false;
			}
		},

		submitEmailForm: function(e) {

			e.preventDefault();

			var self = this;

			if (this.validateEmail($("#bb-emailInput").val())) {

				$(this.$el).addClass('is-loading');

				$.ajax({
					type: 'POST',
					url: $(self.$el).attr('action'),
					data: $(self.$el).serialize()
				}).done(function () {
					self.formSuccess(self.email_feedbacks.send_success);
				}).fail(function () {
					self.formError(self.email_feedbacks.send_error);
				});
			}
			return false;
		},

		formError: function(message) {
			this.formReset();
			$(this.$el).addClass('has-error');

			var self = this;
			$(this.input)
				.val('')
				.attr('placeholder', message);
		},

		formSuccess: function(message) {
			this.formReset();
			$(this.$el).addClass('has-success');

			var self = this;
			$(this.input)
				.val('')
				.attr('placeholder', message);
		},

		formLoading: function() {
			$(this.$el).addClass('is-loading');
		},

		formReset: function(message) {
			$(this.$el)
				.removeClass('has-error')
				.removeClass('has-success')
				.removeClass('is-loading');
			$(this.input).attr('placeholder', this.email_feedbacks.default);
		}
	});

})();
