window.StaticHtml = {
	Models: {},
	Views: {},

	init: function() {
		'use strict';

		// init views
		if ($('#bb-gibsContainer').length) {
			new StaticHtml.Views.Gibs({
				el: '#bb-gibsContainer'
			});
		}
	},
};


$(document).ready(function() {
	'use strict';
	$.ajaxSetup({
		cache: true
	});
	window.StaticHtml.init();
});
