StaticHtml.Models = StaticHtml.Models || {};

(function() {
	'use strict';

	StaticHtml.Models.Clip = Backbone.Model.extend({
		urlRoot: '/api/flyer_gibs/',

		defaults: {
			'page_title': 'Loading',
		},

		parse: function (resp) {
			// fetch data or set fallback locale placeholders
			resp.data.FlyerGib.page_title = resp.data.FlyerGib.settings.layout.header_title !== "" ? resp.data.FlyerGib.settings.layout.header_title : '';

			resp.data.FlyerGib.title = resp.data.FlyerGib.title !== "" ? resp.data.FlyerGib.title : '';

			resp.data.FlyerGib.description = resp.data.FlyerGib.description !== "" ? resp.data.FlyerGib.description : '';

			resp.data.FlyerGib.settings.expires = resp.data.FlyerGib.settings.expires !== "" ? resp.data.FlyerGib.settings.expires : '';

			resp.data.FlyerGib.settings.price = resp.data.FlyerGib.settings.price !== "" ? resp.data.FlyerGib.settings.price : '';

			resp.data.FlyerGib.settings.image_url = resp.data.FlyerGib.settings.image_url !== "" ? resp.data.FlyerGib.settings.image_url : '';

			resp.data.FlyerGib.settings.layout.buttons = typeof(resp.data.FlyerGib.settings.layout.buttons) !== undefined ? resp.data.FlyerGib.settings.layout.buttons : '';

			resp.data.FlyerGib.coupon_layout.buttons.send_btn = typeof(resp.data.FlyerGib.coupon_layout.buttons.send_btn) !== undefined ? resp.data.FlyerGib.coupon_layout.buttons.send_btn : '';
			resp.data.FlyerGib.coupon_layout.buttons.print_btn = typeof(resp.data.FlyerGib.coupon_layout.buttons.print_btn) !== undefined ? resp.data.FlyerGib.coupon_layout.buttons.print_btn : '';
			resp.data.FlyerGib.coupon_layout.form_divider = typeof(resp.data.FlyerGib.coupon_layout.form_divider) !== undefined ? resp.data.FlyerGib.coupon_layout.form_divider : '';

			resp.data.FlyerGib.email_feedbacks = typeof(resp.data.FlyerGib.email_feedbacks) !== undefined ? resp.data.FlyerGib.email_feedbacks : '';

			resp.data.FlyerGib.disclaimer_clip = resp.data.FlyerGib.disclaimer_clip !== "" ? resp.data.FlyerGib.disclaimer_clip : '';
			resp.data.FlyerGib.disclaimer_coupon = resp.data.FlyerGib.disclaimer_coupon !== "" ? resp.data.FlyerGib.disclaimer_coupon : '';

			resp.data.FlyerGib.terms_conditions = resp.data.FlyerGib.terms_conditions !== "" ? resp.data.FlyerGib.terms_conditions : '';
			resp.data.FlyerGib.privacy_policy = resp.data.FlyerGib.privacy_policy !== "" ? resp.data.FlyerGib.privacy_policy : '';

			resp.data.FlyerGib.type = resp.data.FlyerGib.type !== "" ? resp.data.FlyerGib.type : '';

			resp.data.FlyerGib.id = resp.data.FlyerGib.id !== "" ? resp.data.FlyerGib.id : '';
			return resp;
		}

	});

})();
