StaticHtml.Views = StaticHtml.Views || {};

(function() {
	'use strict';

	StaticHtml.Views.Gibs = Backbone.View.extend({

		template: _.template( $('#bb-clipContainer-tpl').html() ),
		templateError: _.template( $('#bb-error-tpl').html() ),
		flyerGibId: null,
		isInIFrame: false,
		isPrintable: false,

		errorValues: {
			errorTitle: '404 Not Found'
		},

		events: {
			'click #bb-printBtn': 'printGib'
		},

		printGib: function() {
			window.print();
		},

		setGibsPadding: function() {
			var buttonsHeight = $('#bb-gibsButtons').innerHeight();

			$(this.el).css({
				paddingBottom: buttonsHeight
			});

			if (this.isInIFrame === true) {
				var pageTitleHeight = $('#bb-pageTitle').innerHeight();
				$(this.el).css({
					paddingTop: pageTitleHeight
				});
			}
		},

		checkIfPrintable: function() {
			this.isPrintable = ( getParameterByName('user-action') === 'print' );
		},

		initialize: function() {

			var self = this;

			this.isInIFrame = (window.location != window.parent.location);

			if (this.isInIFrame === true) {
				$(this.el).addClass('is-in_iframe');
			}

			// detect flyerGibId from querystring
			this.flyerGibId = getParameterByName('flyerGibId');
			if (!this.flyerGibId) {
				// TODO --> return message for missing or invalid Id
				return;
			}

			this.checkIfPrintable();

			if (this.isPrintable) {
				$(this.$el).addClass('is-printable');
			}

			_.bindAll(this, "render");

			this.model = new StaticHtml.Models.Clip({id: this.flyerGibId});

			this.setPageTitle(this.model.defaults.page_title);
			$('#bb-pageTitle').text(this.model.defaults.page_title);

			this.model.fetch({
				success: function() {
					self.render();
					$(self.el).addClass('has-loaded');
				},
				error: function (model, response){
					if (response.status == 404) {
						$(self.el).addClass('has-error');
						self.$el.html( self.templateError ( self.errorValues ) );
						return;
					}
					if (response.status == 500) {
						console.log('500 server error');
						return;
					}
					if (response.status == 400) {
						console.log('400 bad request error');
						return;
					}
				}
			});
		},

		render: function () {
			var FlyerGib = this.model.get('data').FlyerGib;
			var values = {
				page_title: FlyerGib.page_title,
				title: FlyerGib.title,
				description: FlyerGib.description,
				expires: FlyerGib.settings.expires,
				price: FlyerGib.settings.price,
				image_url: FlyerGib.settings.image_url,
				buttons: FlyerGib.settings.layout.buttons,
				send_btn: FlyerGib.coupon_layout.buttons.send_btn,
				print_btn: FlyerGib.coupon_layout.buttons.print_btn,
				form_divider: FlyerGib.coupon_layout.form_divider,
				disclaimer_clip: FlyerGib.disclaimer_clip,
				disclaimer_coupon: FlyerGib.disclaimer_coupon,
				terms_conditions: FlyerGib.terms_conditions,
				privacy_policy: FlyerGib.privacy_policy,
				email_feedbacks: FlyerGib.email_feedbacks,
				type: FlyerGib.type,
				id: FlyerGib.id
			};

			this.email_feedbacks = FlyerGib.email_feedbacks;

			this.setPageTitle(FlyerGib.page_title);

			this.$el.html( this.template ( values ) );

			this.$el.find('img').on('load', function() {
				$(this).addClass('has-loaded');
			});

			this.setGibsPadding();

			if ($('#bb-emailForm').length) {
				new StaticHtml.Views.EmailForm({
					el: '#bb-emailForm',
					email_feedbacks: values.email_feedbacks
				});
			}

			return this;
		},

		setPageTitle: function(title) {
			document.title = title;
		}
	});

})();
